import React from 'react';
import TeamMember from './TeamMember';

export default class Team extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      vpWidth: null
    };

    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.populateMemberArray = this.populateMemberArray.bind(this);
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ vpWidth: window.innerWidth });
  }

  componentWillUnmount() {
    this.state.vpWidth;
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  populateMemberArray() {
    const orderedMembers =
      this.state.vpWidth > 1024
        ? this.props.members
        : this.props.members
            .map(a => ({ sort: Math.random(), value: a }))
            .sort((a, b) => a.sort - b.sort)
            .map(a => a.value);

    return orderedMembers;
  }

  render() {
    const members = this.populateMemberArray();

    return (
      <div className="outer">
        <div className="inner">
          {members.map((item, i) => {
            return (
              <TeamMember
                key={i}
                name={item.name}
                position={item.position}
                bio={item.bio}
                links={item.links}
              />
            );
          })}
        </div>
        <style jsx>{`
          .outer {
            position: relative;
            width: 100vw;
            overflow: hidden;
            scroll-snap-type: x mandatory;
            scroll-snap-points-x: repeat(80vw);
            background-image: url('/static/images/bg.png');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: 50% 10%;
          }

          .outer:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(
              transparent 10%,
              var(--color-quaternary),
              var(--color-quaternary),
              var(--color-quaternary)
            );
          }

          .inner {
            display: flex;
            flex-wrap: nowrap;
            scroll-snap-align: start;
            overflow-x: auto;
            -webkit-overflow-scrolling: touch;
            margin-top: 5rem;
            margin-bottom: -50px; /* maximum width of scrollbar */
            padding-bottom: 50px; /* maximum width of scrollbar */
            padding-left: 10vw;
            overflow-y: hidden;
            overflow-x: scroll;
          }

          @media (min-width: 768px) {
            .inner {
              max-width: 1400px;
              flex-wrap: wrap;
              justify-content: center;
              padding-left: 0;
              margin-left: auto;
              margin-right: auto;
            }
          }
        `}</style>
      </div>
    );
  }
}
